"use client";

import React, { createContext, useContext, useEffect, useState, ReactNode } from 'react';
import { LaserEyesProvider, useLaserEyes } from '@omnisat/lasereyes-react';
import { BTC } from '@omnisat/lasereyes-core';

interface WalletContextType {
  isConnected: boolean;
  address: string | null;
  publicKey: string | null;
  balance: number | null;
  connect: () => Promise<void>;
  disconnect: () => Promise<void>;
  signMessage?: (message: string) => Promise<string>;
  sendBitcoin?: (to: string, amount: number) => Promise<string>;
}

const WalletContext = createContext<WalletContextType | undefined>(undefined);

export function useWallet() {
  const context = useContext(WalletContext);
  if (context === undefined) {
    throw new Error('useWallet must be used within a WalletProvider');
  }
  return context;
}

interface WalletProviderProps {
  children: ReactNode;
}

export function WalletProvider({ children }: WalletProviderProps) {
  const { isConnected, address, publicKey, balance, connect, disconnect } = useLaserEyes();
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
  }, []);

  const walletContextValue: WalletContextType = {
    isConnected: mounted ? isConnected : false,
    address: mounted ? address : null,
    publicKey: mounted ? publicKey : null,
    balance: mounted ? (balance ? Number(balance) / 100000000 : null) : null, // Convert satoshis to BTC
    connect,
    disconnect,
  };

  return (
    <WalletContext.Provider value={walletContextValue}>
      {children}
    </WalletContext.Provider>
  );
}

// This component wraps the entire app with LaserEyes provider
export function WalletProviderWrapper({ children }: { children: ReactNode }) {
  return (
    <LaserEyesProvider
      config={{
        network: 'mainnet', // or 'testnet', 'regtest'
      }}
    >
      <WalletProvider>
        {children}
      </WalletProvider>
    </LaserEyesProvider>
  );
}
